.mobileMenu {
    position: absolute;
    z-index: 100;
    display: block;
    box-sizing: border-box;

    min-width: 320px;
    max-width: 480px;
    width: 100%;
    height: 100%; 
    padding: 20px;

    top: 0;
    left: 50%;
    transform: translateX(-50%);

    background-color: #FEF9F9;

        @media screen and (min-width: 768px) {
            max-width: 768px;
            padding: 24px 32px;
        }
        
        @media screen and (min-width: 1280px) {
            width: 1280px;
            padding: 20px 16px;
        }
}

.head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 28px;
}

.closeButton {
    cursor: pointer;
    stroke: #FFC107;
}

